.section-form {
  padding: 0;
  position: relative;

  &:before {
    content: "";
    background-color: #1f2120;
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 50%;
    z-index: 1;
  }

  input,
  select,
  textarea {
    @extend .form-control;
    border-radius: 2px;
    border: solid 1px #172332;
    background-color: #fff;
    min-height: 50px !important;
    color: #24242450;
    &:hover,
    &:active,
    &:focus {
      outline: 0;
      box-shadow: none;
    }
    &::placeholder {
      color: #24242480;
    }
  }
  p {
    color: white;
  }
  textarea {
    min-width: 200px;
  }
  .container {
    z-index: 100;
    position: relative;
  }

  input.hs-button {
    @extend .btn-secondary;
  }

  .hs_submit {
    @extend .col-12;
    padding-left: calc(var(--bs-gutter-x) * 0.5);
    padding-right: calc(var(--bs-gutter-x) * 0.5);
  }
}

.form-columns-2 > div {
  @extend .col-12, .col-md-6;
  float: inherit;
  width: 100% !important;
  + div {
    margin-top: 20px;
  }
  @include media-breakpoint-up(md) {
    width: 50% !important;
    + div {
      margin-top: 0;
    }
  }
}

.form-columns-1 > div {
  @extend .col-12;
}

form fieldset {
  margin-bottom: 20px !important;
  @extend .row;
}

fieldset > div .input {
  margin-right: 0 !important;
}

.form-container {
  background-color: #172332;
  padding: 100px 80px;

  label {
    display: none;
  }

  label span {
    display: inline-flex;
    flex-direction: row;
    color: white;
    opacity: 0.8;
    font-family: "roboto";
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 10px;
  }

  .hs-error-msgs {
    padding: 0;
    margin: 10px 0 0 0;
    label {
      display: flex;
      color: red;
    }
  }
}

.form-title {
  font-family: "presicav";
  font-size: 38px;
  font-weight: bold;
  line-height: 1.11;
  color: #fff;
  text-align: left;
  margin-bottom: 40px;
  padding: 0 10px;
  @include media-breakpoint-down(md) {
    font-size: 28px;
    line-height: 1.07;
  }
}

.form-text {
  font-family: "roboto";
  font-size: 18px;
  line-height: 1.61;
  color: #fff;
  margin-bottom: 40px;
  padding: 0 10px;
  a {
    color: #fff;
    text-decoration: underline;
    transition: 0.2s all;
    &:hover {
      color: #fff;
      cursor: pointer;
      opacity: 0.8;
    }
  }
}

#hsForm_4a9d00ec-c7a4-41be-9609-42a780735762
  > fieldset:nth-child(3)
  > div.hs-dependent-field
  > div {
  float: none !important;
